@use 'base/variables' as v;
h1,
h2,
h3 {
    margin: 0;
    line-height: 1.5;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.2rem;
}

h3 {
    font-size: 2.4rem;
}

a {
    text-decoration: none;
}

// Utilidad para que cada que finalice con heading aplique los estilos
[class$="__heading"] {
    margin: 2rem 0;
}