/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lexend", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 90%;
  max-width: 900px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
  height: 100%;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 2rem 0;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

[class$=__heading] {
  margin: 2rem 0;
}

.notavif.notwebp .header {
  background-image: url("../img/header.jpg");
  background-image: linear-gradient(rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.1) 100%), url("../img/header.jpg");
}

.webp .header {
  background-image: url("../img/header.webp");
  background-image: linear-gradient(rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.1) 100%), url("../img/header.webp");
}

.avif .header {
  background-image: url("../img/header.avif");
  background-image: linear-gradient(rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.1) 100%), url("../img/header.avif");
}

.header {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding: 5rem 0 10rem 0;
}
.header__busqueda {
  display: grid;
  place-items: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header__busqueda {
    margin-top: 10rem;
  }
}
.header__busqueda-label {
  text-align: center;
  margin-bottom: 1rem;
}
.header__busqueda_boton {
  background-color: #FFFFFF;
  padding: 1rem 2rem;
  color: #e8505b;
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
}
@media (min-width: 768px) {
  .header__busqueda_boton {
    display: inline-block;
  }
}

.barra {
  display: none;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #FFFFFF;
  }
}
.barra__heading {
  color: transparent;
  background: linear-gradient(to right, #FFFFFF 0%, #e8505b 100%);
  background-clip: text;
  -webkit-background-clip: text;
  margin: 0;
}
.barra__link {
  color: #FFFFFF;
  margin-right: 1.5rem;
}
.barra__link:last-of-type {
  margin-right: 0;
}
.barra__link:hover {
  cursor: pointer;
}

.formulario {
  background-color: #FFFFFF;
  margin: 5rem auto 2rem auto;
  border-radius: 5rem;
  box-shadow: 0px 3px 7px 0px #686868;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .formulario {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .formulario {
    display: flex;
    justify-content: space-between;
    gap: unset;
    align-items: center;
    padding: 0;
    min-width: 70rem;
  }
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
}
.formulario__campo:hover {
  background-color: #F0f0f0;
  border-radius: 5rem;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.4);
}
@media (min-width: 768px) {
  .formulario__campo:nth-child(2) {
    grid-column: 2/3;
    grid-row: 2/3;
  }
  .formulario__campo:nth-child(3) {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .formulario__campo:nth-child(4) {
    grid-column: 2/3;
    grid-row: 1/2;
  }
  .formulario__campo:nth-child(5) {
    grid-column: 1/3;
    grid-row: 3/4;
  }
}
@media (min-width: 1024px) {
  .formulario__campo:nth-child(1) {
    padding-left: 5rem;
  }
  .formulario__campo:nth-child(5) {
    padding-right: 5rem;
  }
}
.formulario__boton {
  border: none;
  background-color: #e8505b;
  padding: 1rem 2rem;
  color: #FFFFFF;
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
  background-color: unset;
  background-image: radial-gradient(circle, #c01e5a 0%, #ba1e58 100%);
  margin-top: 0;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .formulario__boton {
    display: inline-block;
  }
}
.formulario__label {
  font-size: 1.5rem;
  font-weight: bold;
}
.formulario__input {
  border: none;
  color: #686868;
  width: 100%;
}

.alojamientos {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .alojamientos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .alojamientos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.alojamiento {
  margin-bottom: 2rem;
  background-color: #e8505b;
  display: grid;
  gap: 0;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
}
@media (min-width: 768px) {
  .alojamiento {
    margin-bottom: 0;
  }
}
@media (min-width: 1024px) {
  .alojamiento {
    flex-direction: row;
    margin-bottom: 0;
  }
}
.alojamiento__heading {
  margin: 0;
  font-size: 2.7rem;
  line-height: 1;
}
.alojamiento__contenido {
  text-align: center;
  padding: 2rem;
}
@media (min-width: 1024px) {
  .alojamiento__contenido {
    text-align: left;
    padding: 2rem 1rem;
  }
}
.alojamiento__heading, .alojamiento__descripcion {
  color: #FFFFFF;
}
.alojamiento__descripcion {
  margin: 1rem 0 0 0;
}

.galeria__grid {
  display: grid;
  grid-template-columns: repeat(4, 33rem);
  column-gap: 4rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.galeria__grid > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .galeria__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    overflow-x: hidden;
  }
}
@media (min-width: 1024px) {
  .galeria__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.lugar {
  padding-bottom: 3rem;
  margin-bottom: 2rem;
}
.lugar__imagen {
  border-radius: 1rem;
}
.lugar__nombre {
  text-align: center;
  font-size: 2rem;
}

.anfitrion {
  border-radius: 1rem;
  margin: 5rem auto;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  row-gap: 10rem;
  padding: 5rem;
  background-image: linear-gradient(to bottom, #000 0%, #000 50%, transparent 51%), url(../img/anfitrion.jpg);
  background-size: 100%, 60rem;
  background-repeat: no-repeat;
  background-position: center, center bottom;
}
@media (min-width: 480px) {
  .anfitrion {
    background-size: 100%, 100%;
  }
}
@media (min-width: 768px) {
  .anfitrion {
    grid-template-rows: unset;
    padding: 10rem;
    grid-template-columns: repeat(2, 1fr);
    background-image: linear-gradient(to right, #000 0%, #000 45%, transparent 50%), url(../img/anfitrion.jpg);
    background-size: 100%, 75rem;
    background-position: center, right center;
  }
}
.anfitrion__btn {
  background-color: #FFFFFF;
  padding: 1rem 2rem;
  color: #000;
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .anfitrion__btn {
    display: inline-block;
  }
}
.anfitrion__heading {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .anfitrion__heading {
    max-width: 70%;
    text-align: center;
  }
}
.anfitrion__descripcion {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .anfitrion__descripcion {
    max-width: 60%;
  }
}

.experiencias__contenido {
  display: grid;
  grid-template-columns: repeat(3, 30rem);
  column-gap: 3rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.experiencias__contenido > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .experiencias__contenido {
    display: grid;
    gap: 1fr;
    grid-template-columns: repeat(3, 1fr);
    overflow-x: hidden;
  }
}

.experiencia {
  border-radius: 1rem;
}
.experiencia__nombre {
  font-weight: 700;
  text-align: center;
}
.experiencia__img {
  border-radius: 1rem;
  max-height: 25rem;
  margin: 0 auto;
}