@use 'variables' as v;
html {
    font-size: 62.5%;
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}

p {
    color: v.$negro;
    font-size: 2rem;
}

.contenedor {
    width: 90%;
    max-width: 900px;
    margin: 0 auto;
}

img {
    max-width: 100%;
    display: block;
    height: 100%;
}

ul {
    padding: 0;
    list-style: none;
    margin: 0;
}

.seccion {
    padding: 2rem 0;
}